import React, { FunctionComponent } from "react";
import ArticlePreview from "components/articlePreview";

interface IPostsList {
  posts: IPost[]
}

const minPostCount = 3

const PostsList:FunctionComponent<IPostsList> = ({ posts }) => {

  return (
    <div className="row">
      {posts.map((post, index) => (
        <div className="col-12 col-md-6 col-lg-4 pb-4" key={index}>
          <ArticlePreview {...post} />
        </div>
      ))}
      
      {posts.length + 1 <= minPostCount && (
        <div className="col-12 col-md-6 col-lg-4 pb-4 d-none d-md-block">
          <ArticlePreview skeletonMode />
        </div>
      )}
      
      {posts.length + 2 <= minPostCount && (
        <div className="col-12 col-md-6 col-lg-4 pb-4 d-none d-lg-block">
          <ArticlePreview skeletonMode />
        </div>
      )}
    </div>
  )
}

export default PostsList
